import Loader from "Components/Loader";
import React, { useState } from "react";

const BackgroundVideo = ({ isMobile, video }) => {
  const [loaded, setLoaded] = useState("");
  console.log(loaded);
  return (
    <div className="main col-12 col-md-10 d-flex justify-content-center ">
      {loaded === "" && (
        <div className="mt-5">
          <Loader />
        </div>
      )}
      <video
        autoPlay
        loop
        muted
        preload="auto"
        style={{ width: `${isMobile ? "100%" : "70%"}` }}
        onCanPlayThrough={(e) => setLoaded(e.type)}
      >
        <source src={video} type="video/mp4" />
      </video>
    </div>
  );
};

export default BackgroundVideo;
